import { profileAPI, RES_PAGE, searchAPI } from "./config";
import { getJSON } from "./helper";


export const state = {
  profile: {},
  search: {
    query: '',
    results: [],
    resultsPerPage: RES_PAGE,
    page: 1,
  },
  total:''
};

export const loadFugitive = async function (id) {
  try {
    const data = await getJSON(`${profileAPI}${id}`);

    let profile = data;
    state.profile = {
      title: profile.title,
      uid: profile.uid,
      description: profile.description,
      details: profile.details,
      suspects: profile.suspects,
      path: profile.path,
      images: profile.images,
      files: profile.files,
      aliases: profile.aliases,
      classification: profile.classification,
      info: [
        ["Age", profile.age_range],
        ["Eyes", profile.eyes],
        ["Hair", profile.hair],
        ["Height max", profile.height_max],
        ["Height min", profile.height_min],
        ["Languages", profile.languages],
        ["Locations", profile.locations],
        ["Nationality", profile.nationality],
        ["NCIC", profile.ncic],
        ["Occupations", profile.occupations],
        ["Place of Birth", profile.place_of_birth],
        ["Possible Countries", profile.possible_countries],
        ["Possible States", profile.possible_states],
        ["Race", profile.race],
        ["Scars and Marks", profile.scars_and_marks],
        ["Sex", profile.sex],
        ["Weight", profile.weight],
      ],
      warningM: profile.warning_message,
      rewardM: profile.reward_text,
      cautions: profile.caution,
      remarks: profile.remarks,
      url: profile.url,
      subject: profile.subjects,
      fieldOffice: profile.field_offices,
    };
    console.log(state.profile);
    
  } catch (err) {
    throw err;
  }
};

const mobile = function() {
    const btnCloseResults = document.querySelector('.results__close')
  const fieldSearchResults = document.querySelector('.search__results')
  const searchResults = document.querySelector('.results')
  const fugitiveCase = document.querySelector('.fugitive__case')
   if(window.screen.width <= 800) {
     fieldSearchResults.style.display = 'none'
    fugitiveCase.style.width = '100vw'
   }
}
mobile()

export const loadSearchFugitive = async function (query) {
  try {
    state.search.query = query
    const respond = await fetch(`https://api.fbi.gov/wanted?pageSize=1000&sort_order=desc&field_offices=${query}`);
    const data = await respond.json();
    console.log(data);

    const results = document.querySelector('.results')
      
      
   
    results.addEventListener('click', function(e) {
      const btnCloseResults = document.querySelector('.results__close')
      const fieldSearchResults = document.querySelector('.search__results')
      const searchResults = document.querySelector('.results')
      const result = document.querySelectorAll('.result')
      const fugitiveCase = document.querySelector('.fugitive__case')

      const targ = e.target.closest('.result')
      console.log(targ);
      result.forEach((res)=>{
        res.classList.remove('active')
      })
      targ.classList.add('active')
    if(window.innerWidth <= 800) {
      
    

      fieldSearchResults.style.display = 'none'
      fugitiveCase.style.width = '100vw'
    }
 })
    state.search.results = data.items.map((item) => {
      return {
        title: item.title,
        uid: item.uid,
        path: item.path,
        images: item.images,
        aliases: item.aliases,
        url: item.url,
        subject: item.subjects,
        audios: item.audios,
        additional: item.additional,
        submit: item.submit,
        stories: item.stories,
        fieldOffice: item.field_offices,
        classification: item.classification,
      };
    });
    state.total = data.total
    console.log(state);

  } catch (err) {
    throw err;
  }
};

