import * as model from "./model";
import profileView from "./views/profileView";
import searchView from "./views/searchView";
import resultsView from "./views/resultsView";
import paginationView from "./views/paginationView";

// if(module.hot) {
//   module.hot.accept()
// }

const controlProfile = async function () {
  try {
    //id
    const id = window.location.hash.slice(1);
    if (!id) return;
    //spinner
    profileView.renderSpinner();

    await model.loadFugitive(id);

    profileView.render(model.state.profile);
  } catch (err) {
    profileView.renderError();
  }
};

// const paginationC = function(goToPage) {
//    model.state.search.page = goToPage
//    return goToPage
// }

const searchResults = async function () {
  try {
    resultsView.renderSpinner()
   
    //search query
    const query = searchView.getQuery()
    if(!query) return
 
    // load search results

    await model.loadSearchFugitive(query);
  
    //render
    resultsView.render(model.getSearchResultsPage());
    
    // // render pagination
    paginationView.render(model.state)

  } catch (err) {
    console.log(`${err}`);
  }
};

const controlPagination = async function(goToPage) {
  
    //render
    resultsView.render(model.getSearchResultsPage(goToPage))
    
    // // render pagination
    paginationView.render(model.state)
}

