import View from './View'
import 'remixicon/fonts/remixicon.css'

class PaginationView extends View {
    _parentElement = document.querySelector('.pagination')

    _generateMarkup() {
        const currentPage = this._data.search.page
        const numPages = Math.ceil(this._data.total / this._data.search.resultsPerPage)
        // page 1 + other pages
        if(currentPage === 1 && numPages > 1) {
            return ` <button data-goto='${currentPage + 1}' class="btn__next btn__page">
                      <span class="page">Page</span>
                      <span class="page__number">${currentPage + 1}</span>
                      <i class="ri-arrow-right-line"></i>
                    </button>`
            
        }
        
        // last page
        if(currentPage === numPages && numPages > 1) {
            return `<button data-goto='${currentPage - 1}' class="btn__prev btn__page">
                      <i class="ri-arrow-left-line"></i>
                      <span class="page">Page</span>
                      <span class="page__number">${currentPage - 1}</span>
                    </button>`
        }
        // other pages
        if(currentPage < numPages) {
            return `<button data-goto='${currentPage - 1}' class="btn__prev btn__page">
                     <i class="ri-arrow-left-line"></i>
                      <span class="page">Page</span>
                      <span class="page__number">${currentPage - 1}</span>
                    </button>
                    <button data-goto='${currentPage + 1}' class="btn__next btn__page">
                      <span class="page">Page</span>
                      <span class="page__number">${currentPage + 1}</span>
                      <i class="ri-arrow-right-line"></i>
                    </button>`
        }
        // page 1 + no pages
        return ''
    }

    addHandlerClick(handler) {
        this._parentElement.addEventListener('click', function(e) {
            const btn = e.target.closest('.btn__page')
            if(!btn) return

            const goToPage = +btn.dataset.goto
            handler(goToPage)
        })
    }
}
export default new PaginationView()